body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;

}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
}
.field-wrapper {
  padding: 20px;
  background-color: bisque;
  width: 80%;
  display: flex;
  }
.column {
  padding: 20px;
  background-color: npmaquamarine;
  width: 100%;
}
textarea {
  resize: none;
    }
#editor {
  
  height: 50vmin;
  border-style: none;
  width: 100%;
  }
#preview {
  text-align: left;
  border:2px;
  border-style: solid;
  border-color: white;
  background-color: white;
  width: 100%;
  height: 50vmin;
}
